.detail-article {
  width: 150px;
  height: 240px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__img {
    height: 200px;
  }

  &__info {
    padding-top: 5px;
    padding-bottom: 6px;
    font-size: 12px;
    font-weight: 600;
    width: 170px;
    height: 110px;
    display: flex;
    flex-direction: column;
    background-color: rgb(47, 179, 255);
    padding-left: 30px;

    color: white;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }

  &__info--name {
    margin: 3px;
  }
}
.returnBtN {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  color: white;
  background-color: grey;
  font-size: 10px;
  border-radius: 5px;
  height: 20px;
  width: 47px;
  margin-top: 15px;
  margin-bottom: 60px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  cursor: pointer;
  margin-left: 147px;
}

.returnBtN:hover {
  color: white;
  background-color: rgb(74, 201, 131);
  transition: width 0.6s;
  cursor: pointer;
}

@media all and (min-width: 1200px) {
  .detail-article {
    width: 700px;
    height: 300px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;

    &__img {
      height: 300px;
    }

    &__info {
      padding-top: 30px;
      font-size: 16px;
      font-weight: 600;
      width: 400px;
      height: 265px;
    }
    &__info--name {
      margin: 16px;
    }
  }
  .returnBtN {
    font-size: 12px;
    height: 27px;
    width: 55px;
    margin-bottom: 40px;
    margin-left: 500px;
  }
} ;
